.popular-news {
  border-radius: 5px;
  background-color: rgb(255, 196, 12);
  padding: 30px 0;
  .popular-title {
    h3 {
      font-size: 25px;
      font-family: 'Rubik', sans-serif;
      color: rgb(51, 51, 51);
      font-weight: bold;
    }
  }
  .popular-slider {
    background: transparent;
    .popular-item {
      margin-right: 20px;
      margin-top: 20px;
      &:hover {
        .item-top {
          img {
            -webkit-transform: scale(1.08);
            transform: scale(1.08);
          }
          .item-hover {
            opacity: .7;
          }
          .read-href {
            opacity: 1;
          }
        }
        .popular-h3 {
          color: #fff;
        }
      }
      .item-top {
        position: relative;
        overflow: hidden;
        border-radius: 5px;
        display: flex;
        margin-top: auto;
        &:hover {
          img {
            -webkit-transform: scale(1.08);
            transform: scale(1.08);
          }
          .item-hover {
            opacity: .7;
          }
          .read-href {
            opacity: 1;
          }
        }
        img {
          width: 100%;
          height: 100%;
          transition: transform .5s;
        }
        .item-hover {
          position: absolute;
          left: 0;
          width: 100%;
          height: 100%;
          background: #000000;
          top: 0;
          opacity: 0;
          transition: opacity .5s;
          z-index: 1;
        }
        .read-href {
          position: absolute;
          left: 0;
          width: 100%;
          top: 50%;
          text-align: center;
          font-size: 18px;
          font-family: 'Rubik', sans-serif;
          color: #fff;
          z-index: 60;
          opacity: 0;
        }
      }
      .popular-h3 {
        font-size: 16px;
        font-family: 'Rubik', sans-serif;
        color: rgb(51, 51, 51);
      }
    }
    button {
      position: absolute;
      top: -7%;
      left: 80%;
      border-style: solid;
      border-width: 1px;
      border-color: rgb(51, 51, 51);
      border-radius: 3px;
      background-color: rgba(255, 196, 12, 0);
      width: 34px;
      height: 34px;
      color: #333333;
      &.slick-next {
        margin-left: 10%;
        &:hover {
          background-color: rgb(51, 51, 51);
          &::before {
            color: #fff;
          }
        }
        &::before {
          content: "\f054";
          font-family: 'FontAwesome';
          font-size: 14px;
          color: #333333;
        }
      }
      &.slick-prev {
        margin-left: 25px;
        .sm-block( {
          margin-left: 0;
        }
        );
        &:hover {
          background-color: rgb(51, 51, 51);
          &::before {
            color: #fff;
          }
        }
        &::before {
          content: "\f053";
          font-family: 'FontAwesome';
          font-size: 14px;
          color: #333333;
        }
      }
    }
  }
}