.content {
  .content-area {
    .news-item {
      margin-bottom: 30px;
      display: flex;
      flex-direction: column;
      .item-top {
        position: relative;
        overflow: hidden;
        border-radius: 5px;
        display: flex;
        &:hover {
          img {
            -webkit-transform: scale(1.08);
            transform: scale(1.08);
          }
          .item-hover {
            opacity: .7;
          }
          .read-href {
            opacity: 1;
          }
        }
        img {
          width: 100%;
          transition: transform .5s;
          height: 100%;
        }
        .item-hover {
          position: absolute;
          left: 0;
          width: 100%;
          height: 100%;
          background: #000000;
          top: 0;
          opacity: 0;
          transition: opacity .5s;
          z-index: 1;
        }
        .read-href {
          position: absolute;
          left: 0;
          width: 100%;
          top: 50%;
          text-align: center;
          font-size: 18px;
          font-family: 'Rubik', sans-serif;
          color: rgb(41, 182, 246);
          z-index: 60;
          opacity: 0;
        }
      }
      .item-body {
        display: flex;
        flex-direction: column;
        .item-title {
          font-size: 16px;
          font-family: 'Rubik', sans-serif;
          color: rgb(51, 51, 51);
          margin: 10px 0;
          display: block;
          &:hover {
            color: rgb(2, 162, 234);
          }
        }
        .item-text {
          font-size: 14px;
          font-family: 'Rubik', sans-serif;
          color: rgb(102, 102, 102);
          margin-bottom: 10px;
        }
        .item-tags {
          font-size: 14px;
          font-family: 'Rubik', sans-serif;
          color: rgb(153, 153, 153);
          display: flex;
          margin-top: auto;
          .news-events {
            align-self: center;
            border-style: solid;
            border-width: 1px;
            border-color: rgb(153, 153, 153);
            background-color: rgba(41, 182, 246, 0);
            padding: 2px 15px;
            border-radius: 20px;
            font-size: 14px;
            font-family: 'Rubik', sans-serif;
            color: rgb(153, 153, 153);
          }
          p {
            &.news-date {
              margin: 0 15px;
            }
            &.eye{
              i{
                margin-right: 5px;
              }
            }
            align-self: center;
            margin-bottom: 0;
          }
        }
      }
    }
  }
  .content-area-lg {
    .news-item {
      display: flex;
      width: auto;
      justify-content: space-between;
      margin-top: 20px;
      .sm-block({
       flex-direction: column;
      });
      .item-top {
        position: relative;
        overflow: hidden;
        border-radius: 5px;
     
        flex-grow: 4;
        flex-shrink: 0;
        .md-block( {
          flex-grow: 2;
          flex-shrink: 2;
        }
        );
        .md-block( {
          width: 100%;
        }
        );
        &:hover {
          img {
            -webkit-transform: scale(1.08);
            transform: scale(1.08);
          }
          .item-hover {
            opacity: .7;
          }
          .read-href {
            opacity: 1;
          }
        }
        img {
          width: 100%;
          transition: transform .5s;
          height: 100%;
          object-fit: cover;
        }
        .item-hover {
          position: absolute;
          left: 0;
          width: 100%;
          height: 100%;
          background: #000000;
          top: 0;
          opacity: 0;
          transition: opacity .5s;
          z-index: 1;
        }
        .read-href {
          position: absolute;
          left: 0;
          width: 100%;
          top: 50%;
          text-align: center;
          font-size: 18px;
          font-family: 'Rubik', sans-serif;
          color: rgb(41, 182, 246);
          z-index: 60;
          opacity: 0;
          .md-block( {
            font-size: 14px;
          });
        }
      }
      .item-body {
        .sm-block({
        padding-left: 0;
        });
        padding-left: 20px;
        flex-grow: 8;
        flex-shrink: .99;
        flex-direction: column;
        .md(width, 100%);
        .item-title {
          font-size: 16px;
          font-family: 'Rubik', sans-serif;
          color: rgb(51, 51, 51);
          font-weight: 700;
          margin: 0 0 10px 0;
          display: block;
       
          .md-block( {
            font-size: 14px;
            height: 43px;
            overflow: hidden;
          }
          );
          .sm-block({
         height: auto;
            });
          &:hover {
            color: rgb(2, 162, 234);
          }
        }
        .item-text {
          font-size: 14px;
          font-family: 'Rubik', sans-serif;
          color: rgb(102, 102, 102);
          margin-bottom: 10px;
          .md-block({
          font-size: 12px;
          height: 75px;
          overflow: hidden;
          });
        }
        .item-tags {
          font-size: 14px;
          font-family: 'Rubik', sans-serif;
          color: rgb(153, 153, 153);
          display: flex;
          margin-top: auto;
          .md-block( {
            font-size: 12px;
          });
          .news-events {
            align-self: center;
            border-style: solid;
            border-width: 1px;
            border-color: rgb(153, 153, 153);
            background-color: rgba(41, 182, 246, 0);
            padding: 2px 15px;
            border-radius: 20px;
            font-size: 14px;
            font-family: 'Rubik', sans-serif;
            color: rgb(153, 153, 153);
            .lg-block( {
              font-size: 12px;
              padding: 2px 10px;
            });
          }
          p {
            &.news-date {
              margin: 0 15px;
            }
            &.eye{
              i{
                margin-right: 5px;
              }
            }
            align-self: center;
            margin-bottom: 0;
          }
        }
      }
    }
  }
  .all-news-btn {
    border-radius: 5px;
    background-color: rgb(255, 196, 12);
    padding: 14px 0;
    width: 162px;
    font-size: 16px;
    font-family: 'Rubik', sans-serif;
    color: rgb(51, 51, 51);
    display: block;
    margin: 40px auto;
    text-align: center;
  }
}

