.main {
  .carousel {
    margin: 30px auto;
    position: relative;
    /*    padding: 30px 20px; */
    .carousel-indicators {
      width: 100%;
      margin-right: 0;
      margin-left: 0;
      bottom: 45px;
      padding: 0 20px;
      right: 0;
      left: 0;
      .md-block( {
        width: 90px;
        margin: auto;
        bottom: -10px;
      }
      );
      li {
        width: 33%;
        margin-right: 0;
        margin-left: 0;
        border-top: 2px solid #ffffff;
        background: transparent;
        text-indent: 0;
        padding-top: 10px;
        flex-wrap: wrap;
        display: flex;
        &.active {
          border-top: 4px solid rgb(41, 182, 246);
          background: transparent;
          transform: translateY(-2px);
          .md-block( {
            border-top: 2px solid rgb(41, 182, 246);
            transform: translateY(0px);
          }
          );
          a {
            color: #ffffff;
          }
        }
        a {
          font-size: 16px;
          .lg(font-size, 14px);
          font-family: 'Rubik', sans-serif;
          width: 309px;
          display: block;
          text-decoration: none;
          color: rgba(255, 255, 255, 0.502);
          line-height: 1.2;
          height: 55px;
          overflow: hidden;
        }
      }
    }
    .carousel-inner {
      .carousel-item {
        .carousel-caption {
          top: 0px;
          left: 20px;
          text-align: left;
          padding-top: 30px;
          .md-block( {
            left: 0;
            width: 100%;
            text-align: center;
            padding: 0;
            padding-top: 15%;
          }
          );
          .media-block( {
            padding-top: 5%;
          }
          , 480px);
          .slider-news {
            background-color: rgb(41, 182, 246);
            padding: 3px 16px;
            font-size: 14px;
            font-family: 'Rubik', sans-serif;
            color: rgb(255, 255, 255);
            border-radius: 20px;
            margin-bottom: 30px;
            display: block;
            width: 84px;
            .lg-block( {
              margin-bottom: 20px;
            }
            );
            .md-block( {
              display: none;
            }
            );
          }
          h5 {
            font-size: 40px;
            font-family: 'Rubik', sans-serif;
            color: rgb(255, 255, 255);
            font-weight: bold;
            width: 60%;
            .xl(width, 75%);
            text-align: left;
            margin-bottom: 10px;
            .lg-block( {
              font-size: 22px;
            }
            );
            .md-block( {
              text-align: center;
              width: 90%;
              font-size: larger;
              margin: auto;
              ;
              margin-bottom: 0;
              height: 70px;
              overflow: hidden;
            }
            );
          }
          i {
            font-size: 14px;
            color: rgb(255, 255, 255);
            margin-bottom: 15px;
            &::before {
              margin-right: 5px;
            }
            .md-block( {
              margin-bottom: 5px;
              font-size: 12px;
            }
            );
          }
          a {
            display: block;
            border-radius: 5px;
            background-color: rgb(255, 196, 12);
            width: 157px;
            padding: 10px;
            font-size: 16px;
            font-family: 'Rubik', sans-serif;
            color: rgb(51, 51, 51);
            text-align: center;
            .lg-block( {
              padding: 6px;
              font-size: 12px;
              width: 130px;
            }
            );
            .md-block( {
              padding: 3px;
              font-size: 12px;
              width: 100px;
              margin: auto;
            }
            );
            i {
              .lg-block( {
                font-size: 12px;
              }
              );
              color: rgb(51, 51, 51);
              margin-bottom: 0;
              font-size: 16px;
            }
          }
        }
      }
    }
  }
}