footer {
    font-family: 'Rubik', sans-serif;
    padding: 40px 0;
    background-color: rgb(34, 34, 34);

    .footer-content {
        display: flex;
.lg-block({
   flex-wrap: wrap; 
 text-align: center;
});
justify-content: center;
        .footer-item {
            width: auto;
            margin: 1%;
            .lg-block({
             width: 45%;
             &:nth-child(1){
                 width: 100%;
                 text-align: center;
             }
             });
            .sm-block({
                width: 100%; 
                text-align: center;
             });
            img {
                width: 190px;
            }

            .footer-nav {
                padding: 0;

                li {
                    list-style: none;

                    &:hover {
                        a {
                            color: rgb(255, 196, 12);
                        }
                    }

                    a {
                        text-decoration: none;
                        font-size: 14px;
                        font-weight: 500;
                        color: #808080;
                    }
                }
            }

            p {
                font-size: 14px;
                color: rgb(153, 153, 153);
                max-width: 325px;
                .sm-block({
                   margin: 0 auto;
                   margin-bottom: 10px;
                 });
                a {
                    color: @yellow;
                }
            }

            .footer-social {
                display: flex;
                padding: 0;
.lg-block({
   justify-content: center; 
});
                li {
                    list-style: none;
                    width: 30px;
                    border-radius: 50%;
                    background: #999999;
                    padding: 3px;
                    text-align: center;
                    margin-right: 10px;

                    &:hover {
                        &:nth-child(1) {
                            background: #3b5998;
                        }

                        &:nth-child(2) {
                            background: #fd0196;
                        }

                        &:nth-child(3) {
                            background: #41b4e6;
                        }

                        &:nth-child(4) {
                            background: #ff9800;
                        }
                        a{
                            i{
                                color: #ffffff;
                            }
                        }
                    }

                    a {
                        text-decoration: none;

                        i {
                            color: #222222;
                        }

                    }
                }
            }
        }
    }
}