header {
  .top-bar {
    background-color: rgb(0, 0, 0);
    padding: 12px;
    .currensy {
      display: flex;
      justify-content: flex-start;
      .media-block( {
        padding-right: 0;
      }
      , 1200px);
      img {
        display: block;
        margin: auto 10px auto 0;
      }
      .currensy-name {
        display: flex;
        justify-content: space-between;
        width: 60%;
        .media-block( {
          font-size: 12px;
        }
        , 1200px);
        p{
            font-size: 14px;
        font-family: 'Rubik',sans-serif,
        sans-serif;
        color: rgb(153, 153, 153);
        text-transform: capitalize;
        margin: 0;
        align-self: center;
        margin-bottom: 0;
        &.valuta{
          
        }
          &.currency-number {
          color: @yellow;
          margin: 0 10px;
       
        }
        }
      
      
        i {
          color: @white;
          align-self: center;
          &::before {
            margin-right: 5px;
          }
          &.fa-caret-up {
            color: @green;
          }
          &.fa-caret-down {
            color: @red;
           
          }
        }
      }
    }
    .form-group {
      margin-bottom: 0;
      position: relative;
      z-index: 2;
      .currensy-select {
        position: relative;
        z-index: 2;
        border-style: solid;
        border-width: 1px;
        border-color: rgb(102, 102, 102);
        border-radius: 3px;
        background-color: transparent;
        width: 100%;
        height: 28px !important;
        font-size: 14px;
        font-family: 'Rubik';
        color: rgb(102, 102, 102);
        text-transform: capitalize;
        text-indent: 5px;
        -moz-appearance: none;
        /* Firefox */
        -webkit-appearance: none;
        /* Safari and Chrome */
        appearance: none;
        .xl-block({
          text-indent: 0;
        });
        &:hover {
          option {
            &::-ms-value {
              background-color: rgb(230, 230, 230);
            }
          }
        }
      }
      &:after {
        content: "\f107";
        font-family: 'FontAwesome';
        position: absolute;
        top: 0;
        border-left: 1px solid rgb(102, 102, 102);
        bottom: 0;
        right: 0;
        width: 35%;
        text-align: center;
      }
      &.active {
        &:after {
          transform: rotate(40deg);
        }
      }
    }
  }
  .sina-nav {
    background-color: rgb(51, 51, 51);
    border: none;
    /*   height: 100px; */
    
  
    .sina-nav-header {
      height: inherit;
      button {
        margin: 20px auto;
      }
    }
    .sina-brand {
      margin: 20px auto;
      width: 283px;
      .sm-block( {
        width: 180px;
        margin-right: 30px;
      }
      );
      .media-block( {
        margin: 10px auto;
      }
      , 1200px);
      img {
        width: 100%;
        padding: 0;
        .sm-block( {
          object-fit: contain;
        }
        );
      }
    }
    .collapse {
      .sina-menu {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .media-block( {
          flex-direction: column;
        }
        , 1024px);
        li {
       
          &:hover {
            a {
              background-color: rgb(34, 34, 34);
              color: @yellow;
            }
          }
          a {
            padding: 40px 10px;
            font-size: 16px;
            font-family: 'Rubik', sans-serif;
            color: #999;
            font-weight: 400;
            position: relative;
            .media-block( {
              text-align: center;
              }
              , 1024px);
            &::before{
              content: "";
              .media-block( {
                background: #fff;
                height: 101%;
                top: -1px;
                }
                , 1024px);
              background: #333333;
              width: 10px;
              height: 100%;
              position: absolute;
              top: 0;
              -webkit-transform: rotate(5deg);
              -ms-transform: rotate(5deg);
              transform: rotate(5deg);
              left: -5px;
          }
          &::after{
            content: "";
            .media-block( {
              background: #fff;
              background: #fff;
              height: 102%;
              top: -1px;
              }
              , 1024px);
            background: #333333;
            width: 10px;
            height: 100%;
            position: absolute;
            top: 0;
            -webkit-transform: rotate(5deg);
            -ms-transform: rotate(5deg);
            transform: rotate(5deg);
            right: -5px;
        }
            .media-block( {
              font-size: 12px;
              padding: 30px 8px;
            }
            , 1200px);
          }
        }
      }
    }
    .extension-nav {
      ul {
        li {
          &:hover {
            a {
              color: @yellow;
            }
          }
          a {
            padding: 40px 10px;
            color: #999;
            font-size: 16px;
            .media-block( {
              padding: 30px 8px;
            }
            , 1200px);
          }
        }
      }
    }
  }
}