header .top-bar {
  background-color: #000000;
  padding: 12px;
}

header .top-bar .currensy {
  display: flex;
  justify-content: flex-start;
}

header .top-bar .currensy img {
  display: block;
  margin: auto 10px auto 0;
}

header .top-bar .currensy .currensy-name {
  display: flex;
  justify-content: space-between;
  width: 60%;
}

header .top-bar .currensy .currensy-name p {
  font-size: 14px;
  font-family: 'Rubik', sans-serif, sans-serif;
  color: #999999;
  text-transform: capitalize;
  margin: 0;
  align-self: center;
  margin-bottom: 0;
}

header .top-bar .currensy .currensy-name p.currency-number {
  color: #ffc40c;
  margin: 0 10px;
}

header .top-bar .currensy .currensy-name i {
  color: #fff;
  align-self: center;
}

header .top-bar .currensy .currensy-name i::before {
  margin-right: 5px;
}

header .top-bar .currensy .currensy-name i.fa-caret-up {
  color: #5dbf36;
}

header .top-bar .currensy .currensy-name i.fa-caret-down {
  color: #ec3129;
}

header .top-bar .form-group {
  margin-bottom: 0;
  position: relative;
  z-index: 2;
}

header .top-bar .form-group .currensy-select {
  position: relative;
  z-index: 2;
  border-style: solid;
  border-width: 1px;
  border-color: #666666;
  border-radius: 3px;
  background-color: transparent;
  width: 100%;
  height: 28px !important;
  font-size: 14px;
  font-family: 'Rubik';
  color: #666666;
  text-transform: capitalize;
  text-indent: 5px;
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  /* Safari and Chrome */
  appearance: none;
}

header .top-bar .form-group .currensy-select:hover option::-ms-value {
  background-color: #e6e6e6;
}

header .top-bar .form-group:after {
  content: "\f107";
  font-family: 'FontAwesome';
  position: absolute;
  top: 0;
  border-left: 1px solid #666666;
  bottom: 0;
  right: 0;
  width: 35%;
  text-align: center;
}

header .top-bar .form-group.active:after {
  transform: rotate(40deg);
}

header .sina-nav {
  background-color: #333333;
  border: none;
  /*   height: 100px; */
}

header .sina-nav .sina-nav-header {
  height: inherit;
}

header .sina-nav .sina-nav-header button {
  margin: 20px auto;
}

header .sina-nav .sina-brand {
  margin: 20px auto;
  width: 283px;
}

header .sina-nav .sina-brand img {
  width: 100%;
  padding: 0;
}

header .sina-nav .collapse .sina-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

header .sina-nav .collapse .sina-menu li:hover a {
  background-color: #222222;
  color: #ffc40c;
}

header .sina-nav .collapse .sina-menu li a {
  padding: 40px 10px;
  font-size: 16px;
  font-family: 'Rubik', sans-serif;
  color: #999;
  font-weight: 400;
  position: relative;
}

header .sina-nav .collapse .sina-menu li a::before {
  content: "";
  background: #333333;
  width: 10px;
  height: 100%;
  position: absolute;
  top: 0;
  -webkit-transform: rotate(5deg);
  -ms-transform: rotate(5deg);
  transform: rotate(5deg);
  left: -5px;
}

header .sina-nav .collapse .sina-menu li a::after {
  content: "";
  background: #333333;
  width: 10px;
  height: 100%;
  position: absolute;
  top: 0;
  -webkit-transform: rotate(5deg);
  -ms-transform: rotate(5deg);
  transform: rotate(5deg);
  right: -5px;
}

header .sina-nav .extension-nav ul li:hover a {
  color: #ffc40c;
}

header .sina-nav .extension-nav ul li a {
  padding: 40px 10px;
  color: #999;
  font-size: 16px;
}

.content .content-area .news-item {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
}

.content .content-area .news-item .item-top {
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  display: flex;
}

.content .content-area .news-item .item-top:hover img {
  -webkit-transform: scale(1.08);
  transform: scale(1.08);
}

.content .content-area .news-item .item-top:hover .item-hover {
  opacity: .7;
}

.content .content-area .news-item .item-top:hover .read-href {
  opacity: 1;
}

.content .content-area .news-item .item-top img {
  width: 100%;
  transition: transform .5s;
  height: 100%;
}

.content .content-area .news-item .item-top .item-hover {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000000;
  top: 0;
  opacity: 0;
  transition: opacity .5s;
  z-index: 1;
}

