.main .carousel {
  margin: 30px auto;
  position: relative;
  /*    padding: 30px 20px; */
}

.main .carousel .carousel-indicators {
  width: 100%;
  margin-right: 0;
  margin-left: 0;
  bottom: 45px;
  padding: 0 20px;
  right: 0;
  left: 0;
}

.main .carousel .carousel-indicators li {
  width: 33%;
  margin-right: 0;
  margin-left: 0;
  border-top: 2px solid #ffffff;
  background: transparent;
  text-indent: 0;
  padding-top: 10px;
  flex-wrap: wrap;
  display: flex;
}

.main .carousel .carousel-indicators li.active {
  border-top: 4px solid #29b6f6;
  background: transparent;
  transform: translateY(-2px);
}

.main .carousel .carousel-indicators li.active a {
  color: #ffffff;
}

.main .carousel .carousel-indicators li a {
  font-size: 16px;
  font-family: 'Rubik', sans-serif;
  width: 309px;
  display: block;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.502);
  line-height: 1.2;
  height: 55px;
  overflow: hidden;
}

.main .carousel .carousel-inner .carousel-item .carousel-caption {
  top: 0px;
  left: 20px;
  text-align: left;
  padding-top: 30px;
}

.main .carousel .carousel-inner .carousel-item .carousel-caption .slider-news {
  background-color: #29b6f6;
  padding: 3px 16px;
  font-size: 14px;
  font-family: 'Rubik', sans-serif;
  color: #ffffff;
  border-radius: 20px;
  margin-bottom: 30px;
  display: block;
  width: 84px;
}

.main .carousel .carousel-inner .carousel-item .carousel-caption h5 {
  font-size: 40px;
  font-family: 'Rubik', sans-serif;
  color: #ffffff;
  font-weight: bold;
  width: 60%;
  text-align: left;
  margin-bottom: 10px;
}

.main .carousel .carousel-inner .carousel-item .carousel-caption i {
  font-size: 14px;
  color: #ffffff;
  margin-bottom: 15px;
}

.main .carousel .carousel-inner .carousel-item .carousel-caption i::before {
  margin-right: 5px;
}

.main .carousel .carousel-inner .carousel-item .carousel-caption a {
  display: block;
  border-radius: 5px;
  background-color: #ffc40c;
  width: 157px;
  padding: 10px;
  font-size: 16px;
  font-family: 'Rubik', sans-serif;
  color: #333333;
  text-align: center;
}

.main .carousel .carousel-inner .carousel-item .carousel-caption a i {
  color: #333333;
  margin-bottom: 0;
  font-size: 16px;
}

