.sidebar {
  border-radius: 5px;
  background-color: rgb(51, 51, 51);
  padding: 10px 20px;
  padding-bottom: 20px;

  .sidebar-title {
    .lg-block( {
      text-align: center;
    }

    );

    h3 {
      font-size: 25px;
      color: rgb(255, 255, 255);
      font-weight: bold;
    }
  }

  .currency {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background: #ffffff;
      margin: 15px auto 0 auto;
    }

    .sm-block( {
      justify-content: center;
    }

    );

    .currensy-row {
      width: 100%;
      .lg-block( {
        width: 40%;
      }

      );

      .sm-block( {
        width: auto;
      }

      );

      .currensy-col {
        display: flex;
        padding: 5px 0;

        img {
          display: block;
          margin: auto 10px auto 0;
        }

        .currensy {
          display: flex;
          justify-content: space-between;
          width: 90%;
          font-size: 16px;
          color: rgb(255, 255, 255);
          font-family: 'Rubik', sans-serif;
          text-transform: capitalize;
          margin: 8px 0;
          align-self: center;

          .currensy-name {
            margin-bottom: 0;
            width: 30%;
          }

          .media-block( {
            font-size: 12px;
          }

          , 1200px);

          .currency-number {
            color: @white;
            margin: 0 10px;
            width: 30%;
          }

          i {
            color: @white;
            width: 20%;

            &::before {
              margin-right: 5px;
            }

            &.fa-caret-up {
              color: @green;
            }

            &.fa-caret-down {
              color: @red;
            }
          }
        }
      }
    }

    .converter-row {
      display: flex;
      flex-wrap: wrap;
      padding: 15px 0;

      .lg-block( {
        width: 50%;
        padding: 0;
      }

      );

      .sm-block( {
        width: auto;
      }

      );

      .converter-col {
        display: flex;
        justify-content: space-between;
        margin: 10px 0;
        width: 100%;

        input {
          width: 35%;
          text-align: right;
          border-radius: 5px;
          background-color: rgb(255, 255, 255);
          height: 45px;

          &::placeholder {
            font-size: 16px;
            font-family: 'Rubik', sans-serif;
            color: rgb(51, 51, 51);
          }
        }

       .form-group {
          position: relative;
          margin-bottom: 0;
          width: 55%;
          background: #ffffff;
        border-radius: 5px;
          select {
            position: relative;
            z-index: 2;
         border: none;
            background-color: transparent;
            height: 45px;
            font-size: 16px;
            font-family: 'Rubik', sans-serif;
            color: rgb(51, 51, 51);
width: 100%;
-moz-appearance: none;
/* Firefox */
-webkit-appearance: none;
/* Safari and Chrome */
appearance: none;
          }

          &::after {
            content: "\f107";
            font-family: 'FontAwesome';
            position: absolute;
            font-size: 25px;
            top: 0;
            bottom: 0;
            right: 0;
            background-color: rgb(255, 196, 12);
            width: 45px;
            height: 45px;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
            text-align: center;
            line-height: 1.9;
          }
        } 
      
     
      }
    }
  }

  .email-row {
    form {
      padding-top: 30px;
      padding-bottom: 10px;
      font-family: 'Rubik', sans-serif;

      .social-title {
        h3 {

          margin-bottom: 10px;
          /* width: 211px; */
        }
      }

      .form-group {
        margin: 0;
        padding-bottom: 10px;

        .lg-block( {
          text-align: center;
        }

        );

        label {
          font-size: 16px;
          color: rgb(255, 255, 255);
          margin-bottom: 15px; // width: 274px;
        }

        .form-control {
          border-radius: 5px;
          background-color: rgb(255, 255, 255);
          border: none;
          /*     width: 309px;*/
          height: 45px;
        }
      }

      .send-btn {
        font-size: 16px;
        color: rgb(51, 51, 51);
        border-radius: 5px;
        background-color: rgb(255, 196, 12);
        padding: 10.5px;
        width: 100%;
        margin: 20px auto;
        border: none;
        /*   width: 309px;
          height: 45px; */
      }
    }

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background: #ffffff;
      margin: 10px auto 0 auto;
    }
  }

  .sidebar-news-row {
    padding-top: 25px;
    display: flex;
    flex-wrap: wrap;

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background: #ffffff;
      margin: 10px auto 0 auto;
    }

    .social-title {
      h3 {
        font-size: 25px;
        color: rgb(255, 255, 255);
        font-weight: bold;
        margin-bottom: 5px;
      }
    }

    .sidebar-news-col {
      display: flex;
      margin: 15px 0;
      flex-basis: 100%;

      .lg-block( {
        flex-basis: 50%;
      }

      );

      .sm-block( {
        flex-basis: 100%;
      }

      );

      .news-img {
        flex-grow: 4;
        flex-shrink: 0;

        .md-block( {
          display: none;
        }

        );

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .news-body {
        flex-grow: 8;
        flex-shrink: 0.99;
        padding-left: 15px;

        .lg-block( {
          flex-shrink: 1;
        }

        );

        &:hover {
          .news-title {
            color: @yellow;
          }
        }

        .news-title {
          font-size: 14px;
          color: rgb(255, 255, 255);
          font-weight: 700;
          display: block;
          height: 46px;
          overflow: hidden;
          line-height: 15px;
        }

        .item-tags {
          display: flex;
          margin-top: 10px;

          .news-events {
            border-style: solid;
            border-width: 1px;
            border-color: rgb(153, 153, 153);
            background-color: rgba(41, 182, 246, 0);
            border-radius: 20px;
            font-size: 12px;
            color: rgb(153, 153, 153);
            padding: 1.4% 6.7%;
            align-self: center;
            text-align: center;
            /*   width: 75px;
          height: 24px; */
          }

          .eye {
            font-size: 12px;
            color: rgb(153, 153, 153);
            margin: 0 10px;
            align-self: center;
            display: block;

            i {
              margin-right: 5px;
            }
          }
        }
      }
    }
  }

  .sidebar-social {

    font-family: 'Rubik', sans-serif;

    .sidebar-title {
      h3 {
        margin: 30px 0;
        margin-bottom: 15px;
      }
    }

    .social {
      display: flex;
      flex-wrap: wrap;

      .sm-block( {
        justify-content: center;
      }

      );

      .social-item {
        flex-basis: 50%;

        .xl-block( {
          flex-basis: 100%;
        }

        );

        .lg-block( {
          flex-basis: 50%;
        }

        );

        .sm-block( {
          flex-basis: auto;
        }

        );
        display: flex;
        text-decoration: none;
        margin: 10px auto;

        &:hover {
          .social-count {
            p {
              color: @yellow;
            }
          }
        }

        .social-logo {
          flex-grow: 2;
          flex-shrink: 0;
          align-self: center;

          .xl-block( {
            flex-grow: 0;
          }

          );
        }

        .social-count {
          flex-grow: 4;
          padding-left: 10px;

          .xl-block( {
            align-self: center;
          }

          );

          p {
            margin: 0;
            line-height: 18px;
            font-size: 16px;
            color: rgb(153, 153, 153);

          }
        }
      }
    }
  }
}